html, body {
  height: 100%;
  font-family: 'arial', sans-serif;
}

header {
  /* grid-area: header; */
  font-size: 4rem;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
  height: 80px;
  width: 100%;
  background-color: #9FEDD7;
}

.body {
  position: absolute;
  top: 80px;
  bottom: 3rem;
  width: 100%;
  padding-top: 10px;
}

footer {
  /* grid-area: footer; */
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  height: 3rem;
  width: 100%;
}
